@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';



























































































































































































































































































































.headMemo {
  margin-bottom: 20px;
  background-color: #fff;
}
.memoTitle {
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  background-color: #f4f5f6;
}
.memoTitle > span {
  width: 52px;
  font-size: 12px;
  display: inline-block;
  margin-left: 20px;
  margin-right: 10px;
}
.memoTitle strong {
  width: 36px;
  font-size: 12px;
  font-weight: 400;
  margin-left: 10px;
  margin-right: 20px;
  display: inline-block;
}
.memoMain {
  margin: 0 20px;
}
