@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';









































































































































































































































































































































































































































































































































































































































































































































.temporaryTask {
  height: 282px;
  margin-bottom: 20px;
  margin-top: 20px;
  background-color: #fff;
}
.temporaryHead {
  height: 35px;
  line-height: 35px;
  display: flex;
  justify-content: space-between;
  border-bottom: solid 1px #ddd;
}
.temporaryHead p {
  margin: 0 0 0 20px;
  font-size: 14px;
}
.temporaryBox {
  height: 177px;
  margin-top: 10px;
  overflow-y: scroll;
  padding-left: 20px;
  padding-right: 20px;
}
.temporaryBox > span {
  cursor: pointer;
  display: inline-block;
  margin: 0px 0px 10px 20px;
}
.temporaryFoot {
  display: flex;
  justify-content: space-between;
  border-top: solid 1px #ddd;
}
.footBtn {
  margin-top: 10px;
  height: 40px;
  margin-right: 20px;
}
.taskSet {
  font-size: 14px;
  padding-bottom: 10px;
  display: inline-block;
}
.demo-ruleForm {
  margin-bottom: -20px;
}
.el-select {
  width: 100%;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
