.addSinhle {
  height: 282px;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #fff;
}
.addSinhleHead {
  height: 33px;
  line-height: 33px;
  display: flex;
  justify-content: space-between;
  border-bottom: solid 1px #ddd;
}
.addSinhleHead p {
  margin: 0 0 0 20px;
  font-size: 13px;
}
.addSence {
  height: 240px;
  margin-top: 10px;
  padding-right: 20px;
}
.addSenceBox {
  height: 193px;
  overflow-y: scroll;
}
.addSenceFoot {
  height: 47px;
}
.addTask {
  margin-left: 20px;
}
.addTaskType {
  height: 200px;
}
.addSinhleShow {
  overflow: hidden;
  height: 150px;
  overflow-y: scroll;
}
.moreShow {
  margin-top: 20px;
}
.el-select {
  width: 100%;
}
.relevanceFoot {
  height: auto;
  margin: 0 20px 0 0;
  padding: 0;
  border-top: solid 1px #ddd;
}
