@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';








































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































.headMagnetNav {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  overflow: auto;
}
.headMagnetNav li {
  width: 18.5%;
  height: auto;
  margin-right: 18px;
  border-top: solid 4px rgba(47, 106, 255, 1);
  display: inline-block;
  background-color: #fff;
}
.headMagnetNav li:last-child {
  margin-right: 0;
}
.headMagnetNav li span {
  display: block;
  margin: 10px;
}
.headMagnetNav li strong {
  display: block;
  float: right;
  padding: 0 20px 29px 0;
}
.taskCentre {
  margin: 20px 20px 0px 0;
  background-color: #fff;
}
.tabsMain {
  display: flex;
  justify-content: space-between;
  background-color: #f4f5f6;
}
.tabsMainLeft {
  display: flex;
  height: 30px;
  line-height: 30px;
  margin: 15px 0;
}
.tabsMainLeft > span {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 14px;
}
.chooseState {
  margin: 15px 20px 0px 0;
  .el-button--primary {
    background-color: #66b1ff;
    border-color: #66b1ff;
  }
}
.queryBox {
  display: flex;
  height: 61px;
  margin: 0 20px;
}
.querySearch {
  display: flex;
  padding-top: 10px;
  margin-left: 10px;
}
.queryDate {
  display: flex;
}
.showMore {
  cursor: pointer;
  color: rgba(47, 106, 255, 1);
}
.selectedTag {
  border-radius: 12px;
  color: #fff !important;
  background-color: rgba(47, 106, 255, 1) !important;
}
.el-tag {
  min-width: 62px;
  text-align: center;
  border: none;
  margin-right: 20px;
  background-color: #fff;
  font-size: 14px;
  color: #334066;
  cursor: pointer;
}
.tagsClass {
  display: flex;
}
.queryTag {
  margin: 0 20px 10px;
  .el-form-item__label {
    width: 76px !important;
  }
}
.sponsorTask {
  width: 50%;
  position: absolute;
  top: 7px;
  left: 120px;
}
.chooseScene {
  margin-bottom: 10px;
}
.chooseScene > span {
  margin-right: 5px;
}
.querySceneName {
  display: flex;
  margin-top: 20px;
}
.querySceneName span {
  margin-top: 10px;
}
