@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';













































































































































































































































.workbench {
  width: 100%;
  height: auto;
  display: flex;
}
.containerLeft {
  width: 70%;
  height: 100%;
  margin-bottom: 10px;
}
.containerRight {
  width: 30%;
  height: 100%;
  margin-bottom: 10px;
}
.fastNav {
  height: auto;
  margin-bottom: 20px;
  background-color: #fff;
}
.rightHead {
  height: 34px;
  line-height: 34px;
  background-color: #f4f5f6;
}
.rightHead > span {
  font-size: 14px;
  padding-left: 20px;
}
.rightMain {
  display: flex;
  flex-wrap: wrap;
}
